$left-color:  #242e4d;
$right-color: #897e79;
$green-dark:  #35c3c1;
$green-light: #00d6b7;
$gray:        #8f8f8f;
$gray-light:  #f5f6f8;

* {
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}

html, body1 { height: 100%; }
.body1 {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  background-color: #b4c6f0;
  
  
  font-family: 'Roboto', helvetica, arial, sans-serif;
  font-size: 1.5em;
  
  &:before {
    content: '';
    position: absolute;
    top: 0; left: 0;
    height: 100%; width: 100%;
    background-size: 300px;
    
    // Noise effect
   // background-image: url(data:image/png;base64,);
    opacity: .3;
  }
}

html, body11 { height: 100%; }
.body11 {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  background-color: #b4c6f0;
  
  
  font-family: 'Roboto', helvetica, arial, sans-serif;
  font-size: 1.5em;

}

#login-head {
  width: 250px;
  height: 20px;
  color: black;
  text-align: center;
}

.login-form1 {
  width: 100%;
  padding: 2em;
  position: relative;
  //background: rgba( #b4c6f0, .15);
  background-color: #b4c6f0;
  
  &:before {
    content: '';
    position: absolute;
    
    background: linear-gradient(
      to right,
      $green-dark,
      $green-light
    );    
  }
  
  @media screen and (min-width: 600px) {
    width: 50vw;
    max-width: 15em;
  }
}

  .flex-row {
    display: flex;
    margin-bottom: 1em;
  }
  .flex-row1 {
    display: flex;
    margin-bottom: 1em;
    margin-left: 60px;
  }
  .error {
    font-size: smaller;
    color: red;
  }
  .chart{
    width: 40%;
  }
  .lf--label {
    width: 4em;
    display: flex;
    align-items: center;
    justify-content: center;
    
    background: $gray-light;
    cursor: pointer;
  }
  .lf--input {
    flex: 1;
    padding: 1em;
    border: 0;
    color: #000000;
    font-size: 1rem;

    &:focus {
      outline: none;
      transition: transform .15s ease;
      transform: scale(1.1);
    }
  }

  .lf--submit {
    display: block;
    padding: 1em;
    width: 100%;
    border-radius: 10px;
    background: linear-gradient(
      to right,
      $green-dark,
      $green-light
    );
    border: 0;
    color: #fff;
    cursor: pointer;
    font-size: medium;
    font-weight: 600;
    text-shadow: 0 1px 0 rgba(black, .2);
    
    &:focus {
      outline: none;
      transition: transform .15s ease;
      transform: scale(1.1);
    }
  }

.lf--forgot {
  margin-top: 1em;
  color: $green-light;
  font-size: .65em;
  text-align: center;
  position: relative;
}

::placeholder { color: $gray; }